function setBlogItemBg() {
    let desktopBg = $(".blog-item__wrap").attr("data-desktop-style")
    let mobBg = $(".blog-item__wrap").attr("data-mob-style")
    let style

    let innerWidth = window.innerWidth


    if(innerWidth > 576){
        style = desktopBg
    } else{
        style = mobBg
    }

    $(".blog-item__wrap").css("background",style)
}


setBlogItemBg()

$(window).resize(function () {
    setBlogItemBg()
})
